export default {
	name: 'UniGrid',
	props: {
		// 每列显示个数
		column: {
			type: Number,
			default: 3
		},
		// 是否显示边框
		showBorder: {
			type: Boolean,
			default: true
		},
		// 边框颜色
		borderColor: {
			type: String,
			default: '#e5e5e5'
		},
		// 全局标记水平方向移动距离 ，起点为中心，负数为左移动，正数为右移动
		hor: {
			type: Number,
			default: 0
		},
		// 全局标记垂直方向移动距离 ，起点为中心，负数为上移动，正数为下移动
		ver: {
			type: Number,
			default: 0
		},
		// 是否正方形显示,默认为 true
		square: {
			type: Boolean,
			default: true
		},
		highlight: {
			type: Boolean,
			default: true
		}
	},
	provide() {
		return {
			grid: this
		}
	},
	data() {
		const elId = `Uni_${Math.ceil(Math.random() * 10e5).toString(36)}`
		return {
			index: 0,
			elId,
			width: 0
		}
	},
	created() {
		this.children = []
		this.index = 0
	},
	mounted() {
		setTimeout(() => {
			this._getSize((width) => {
				this.children.forEach((item, index) => {
					item.width = width
				})
			})
		}, 50)
	},
	methods: {
		change(e) {
			this.$emit('change', e)
		},
		_getSize(fn) {

			uni.createSelectorQuery()
				.in(this)
				.select(`#${this.elId}`)
				.boundingClientRect()
				.exec(ret => {
					this.width = parseInt(ret[0].width / this.column) - 1 + 'px'
					fn(this.width)
				})







		}
	}
}
